import React, { Component } from 'react';
import EducationIllus from '.././illustration/boy-learn.jpg';
import GradesCuate from '.././illustration/Grades-cuate.svg';

function LoginWidget(){

  return (

      <div className="desktop_biggest_body">

        <i className="fi-rr-home home_btn"></i>
        
        <div className="desktop_left_body">
          <div className="left_body_item">
            <img
              className="left_body_illustration"
              src={GradesCuate}
            />
            <h2 className="left_body_illustration_header">
              <span className="header_span_brown">Edu</span>
              <span className="header_span">Lens</span> 
            </h2>

            <p className="left_body_illustration_main_text">
              Lorem ipsum, dolor sit amet consectetur adipisicing elit. Possimus
              laboriosam praesentium, modi provident consequuntur voluptate.
              Voluptates quia tempora earum, distinctio ad molestiae dolore
              similique nisi et quis, tempore soluta porro.
            </p>
          </div>

          <div className="dot_element">
            <span className="dot_element_span"></span>
            <span className="dot_element_span"></span>
            <span className="dot_element_span"></span>
            <span className="dot_element_span"></span>
            <span className="dot_element_span"></span>
          </div>
        </div>

        <div className="desktop_right_body">
          <div className="dual_top_cont">
            <p className="dual_top"> Login </p>
          </div>

          <div className="form_block" id="login_form_block">
            <form className="form_cont" id="login_form_final">
              <div className="login_icon_cont">
                <i
                  className="fi-rr-user"
                  type="login_illustration_icon"
                ></i>
              </div>

          <div>
          <i className="fi-rr-user input_field_icon"></i>
              <input
                className="login_input"
                name="email"
                type="text"
                id="login_id"
                placeholder="Email Address"
              />
              
            </div>

        <div>
        <i className="fi-rr-lock-alt input_field_icon"></i>
              <input
                className="login_input"
                name="password"
                id="login_pwd"
                type="password"
                placeholder="Password"
              />
              <i className="fi-rr-eye input_right_icon"></i>
              <p className="forgotten"> Forgotten Password? </p>
          </div>

              <p id="login_output"></p>
                <button className="move_to_create" id="login_btn_final">
                  Log In 
                  <i className="fi-rr-sign-in right_icon"></i>
                </button>

                
              
            </form>
          </div>
        </div>
      </div>
    );
}

export default LoginWidget;
