import React, {Component, useState, useEffect, useRef} from 'react';
import lottie from 'lottie-web';
import HashLoader from 'react-spinners/HashLoader';
import CbtAnimate from '.././LottieAnimation/cbtAnimate';
import Adewale from '../.././images/coder.jpg';
import WAEC from '../.././images/Waec_logo.jpg';
import UTME from '../.././images/jamb_logo.png';
import Numeracy from '../.././images/numeracy_test.svg';
import BrainTest from '../.././images/brain_test.jpeg';
import {Link} from 'react-router-dom';
import MainCbtNav from '.././cbtComponents/cbtItemWidgets';

function CbtTest (){

    const [loading, setLoading] = useState(false);

    useEffect( () =>{
        setLoading(true)
        setTimeout(() =>{
            setLoading(false)
        }, 5000)
    }, [] )

    return(
        <div id="full_project_body_container">

            {loading ? 
             
            <div className="animation_widget">
                <HashLoader 
                loading={loading}
                size={90} 
                 />
            </div>

            : 

            <div>
                <div className="each_tab_cont">
                <p className="instrution"><i class="fas fa-user-circle beside_icon"></i> Welcome, Adewale! </p>
                

                <CbtAnimate />

                <p className="instrution">Select a test type to begin! </p>

                <div className="iblock_widget">
                    <div className="iblock_widget_item">
                        <img className="iblock_widget_img" src={UTME} alt="" />
                        <p><i className="fas fa-archway beside_icon"></i>UTME Test</p>
                    </div>

                <Link to="/waec-subjects">
                    <div className="iblock_widget_item">
                        <img className="iblock_widget_img" src={WAEC} alt="" />
                        <p><i className="fas fa-certificate beside_icon"></i>WAEC Test</p>
                    </div>
                    </Link>

                    <div className="iblock_widget_item">
                        <img className="iblock_widget_img" src={BrainTest} alt="" />
                        <p><i className="fas fa-brain beside_icon"></i>IQ Test</p>
                    </div>

                    <div className="iblock_widget_item">
                        <img className="iblock_widget_img" src={Numeracy} alt="" />
                        <p><i className="fas fa-square-root-alt beside_icon"></i>Mathematics Test</p>
                    </div>

                </div>

                <button className="more_recom"> Load more Test <i className="fas fa-arrow-right right_icon"></i></button>
                </div>
            </div>



            }
        </div>
    );
}

export default CbtTest;

