import React, { Component } from "react";
import {BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";

class RightBarWidget extends Component {
  render() {
    return (
      <div className="index_left_bar">
        <div className="left_bar_item_nav">
          <Link to="/">
            <div className="left_bar_item_cont">
              <div className="left_bar_item_list">
                <i className="fi-rr-home" type="left_bar_icons"></i>
                <p className="left_bar_item">Home</p>
                
              </div>
            </div>
          </Link>

          <Link to="/education-log">
            <div className="left_bar_item_cont">
              <div className="left_bar_item_list">
              <i className="fi-rr-graduation-cap" type="left_bar_icons"></i>
                <p className="left_bar_item"> Education Widget</p>
                
              </div>
            </div>
          </Link>

          <Link to="/cbtTest">
          <div className="left_bar_item_cont">
            <div className="left_bar_item_list">
            <i className="fi-rr-keyboard" type="left_bar_icons"></i>
              <p className="left_bar_item">CBT Mock Test </p>
              
            </div>
          </div>
          </Link>

          <Link to="/brainstorm">
          <div className="left_bar_item_cont">
            <div className="left_bar_item_list">
            <i className="fi-rr-checkbox" type="left_bar_icons"></i>
              <p className="left_bar_item">Brainstorm App</p>
              
            </div>
          </div>
          </Link>

          <Link to="/cgpa-calculator">
          <div className="left_bar_item_cont">
            <div className="left_bar_item_list">
            <i className="fi-rr-calculator" type="left_bar_icons"></i>
              <p className="left_bar_item">CGPA Calculator</p>
            </div>
          </div>
          </Link>

          <div className="left_bar_item_cont">
            <div className="left_bar_item_list">        
            <i className="fi-rr-marker" type="left_bar_icons"></i>
              <p className="left_bar_item">Schools Map</p>
              
            </div>
          </div>

        <Link to="/generate-institution">
          <div className="left_bar_item_cont">
            <div className="left_bar_item_list">
            <i className="fi-rr-stats" type="left_bar_icons"></i>
              <p className="left_bar_item"> Institution Generator</p>
              
            </div>
          </div>
          </Link>

          <div className="left_bar_item_cont">
            <div className="left_bar_item_list">         
            <i className="fi-rr-e-learning" type="left_bar_icons"></i>
              <p className="left_bar_item">Learn Online</p>
              
            </div>
          </div>

          <div className="left_bar_item_cont">
            <div className="left_bar_item_list">
            <i className="fi-rr-power" type="left_bar_icons"></i>
              <p className="left_bar_item">Log off</p>
             
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default RightBarWidget;
