import React, { Component } from 'react';
import {BrowserRouter as Router, Switch, Route} from 'react-router-dom';
import RightBarWidget from './rightBarComp';
import HeaderComp from '.././components/headerComponent';
import EduCenterWidget from './mainAppComponents/eduCenterWidget';
import SocialWidget from './mainAppComponents/socialWidget';
import CgpaCalculatorWidget from './mainAppComponents/cgpaCalcWidget';
import BrainstormIndexWidget from './mainAppComponents/brainstormWidget';
import CbtTest from './mainAppComponents/cbtTest';
import InfoSideWidget from './infoWidget';
import WaecYear from './cbtComponents/waecyear';
import WaecSubj from './cbtComponents/waecSubject';
import WaecQuestion from './cbtComponents/waecQuestion';
import LoginWidget from './login';
import GenerateInstitution from './mainAppComponents/generateInsCou';
import CreateSession from './brainstormComponents/createSession';
import CreateQuestion from './brainstormComponents/createQuestion';
import Notification from './notification';
import Chat from './chat';


class MainAppBody extends Component {
    render(){
        return(
            <React.Fragment>
                    <Router basename="/edulensRjs">

                        <HeaderComp />

                        <Switch>

                            <Route path="/chat">
                                <Chat />
                            </Route>
                        
                            <div className="index_biggest_cont">
                                    <RightBarWidget />
                                    
                                        <Route path="/" exact>
                                            <SocialWidget />
                                        </Route>

                                        <Route path="/education-log" >
                                            <EduCenterWidget />
                                        </Route>

                                        <Route path="/Login">
                                            <LoginWidget  />
                                        </Route>

                                        <Route path="/notification">
                                            <Notification />
                                        </Route>

                                        

                                        <Route path="/cgpa-calculator" component={CgpaCalculatorWidget} />
                                        <Route path="/brainstorm" component={BrainstormIndexWidget} />
                                        <Route path="/cbtTest" component={CbtTest} />
                                        <Route path="/waec-subjects" component={WaecSubj} />
                                        <Route path="/waec-year" component={WaecYear} />
                                        <Route path="/waec-questions" component={WaecQuestion} />
                                        <Route path="/generate-institution" component={GenerateInstitution} />
                                    
                                    <Route path="/create-question">
                                            <CreateQuestion />
                                        </Route>
                                    
                                    <Route path="/create-session">
                                            <CreateSession />
                                        </Route>
                                        
                                    
                                    <InfoSideWidget />
                            
                            </div>

                    </Switch>
                    </Router>
            </React.Fragment>
        );
    }
}

export default MainAppBody;