import React, { Component, useState, useEffect, useRef } from "react";
import HashLoader from 'react-spinners/HashLoader';

function CgpaCalculatorWidget() {

          const  [loading, setLoading] = useState(false);
          useEffect( () => {
              setLoading(true)
              setTimeout(() => {
                setLoading(false)
            }, 5000)
          }, []);
        

    return (

      <div id="full_project_body_container">

      {loading ?

          <div className="animation_widget">
              <HashLoader
              loading={loading} 
              size={90}
              marginTop={150}
              alignSelf={'center'}
              color={"black"}
                  />
          </div>

            :

      <div>
        <div className="each_tab_cont">
          <p className="user_suggestion_header">
            <i className="fas fa-calculator beside_icon"></i>CGPA Calculator
          </p>

          <form
            className="select_subject_form"
            action="brainstorm-players-interface.php"
          >
            <p className="instrution"> Select total number of Courses!</p>

            <GradePointSelectInput />

            <p className="instrution"> Select total Course unit</p>

            <SelectTotalCourseUnit />

            <div className="gp_input_container">
              <div className="gp_log_header_cont">
                <span className="gp_log_header">S/N</span>
                <span className="gp_log_header">Course Grade</span>
                <span className="gp_log_header">Course Unit</span>
              </div>

              <div>
                <span className="numbering">01.</span>
                <GradeLittleInput />

                <CUnitInput />
              </div>

              <div>
                <span className="numbering">02</span>
                <GradeLittleInput />

                <CUnitInput />
              </div>

              <div>
                <span className="numbering">03</span>
                <GradeLittleInput />

                <CUnitInput />
              </div>

              <div>
                <span className="numbering">04</span>
                <GradeLittleInput />

                <CUnitInput />
              </div>

              <div>
                <span className="numbering">05</span>
                <GradeLittleInput />

                <CUnitInput />
              </div>

              <div>
                <span className="numbering">06</span>
                <GradeLittleInput />

                <CUnitInput />
              </div>

              <div>
                <span className="numbering">07</span>
                <GradeLittleInput />

                <CUnitInput />
              </div>

              <div>
                <span className="numbering">08</span>
                <GradeLittleInput />

                <CUnitInput />
              </div>

              <div>
                <span className="numbering">09</span>
                <GradeLittleInput />

                <CUnitInput />
              </div>

              <div>
                <span className="numbering">10.</span>

                <GradeLittleInput />

                <CUnitInput />
              </div>
            </div>

            <button className="move_to_create">Calculate</button>
          </form>
        </div>

        <div className="each_tab_cont">
          <p className="user_suggestion_header"> <i class="fas fa-calculator beside_icon"></i>CGPA Calculator</p>


          <form
            className="select_subject_form"
            action="brainstorm-players-interface.php"
          >
            <p className="instrution"> Select number of semesters!</p>

            <select
              className="select_subject_bar"
              name="select_number_question"
            >
              <option className="brainstorm_subject" value="">
                {" "}
                Select number of semesters...{" "}
              </option>
              <option className="brainstorm_subject" value="">
                {" "}
                2{" "}
              </option>
              <option className="brainstorm_subject" value="">
                {" "}
                3{" "}
              </option>
              <option className="brainstorm_subject" value="">
                {" "}
                4{" "}
              </option>
              <option className="brainstorm_subject" value="">
                {" "}
                5{" "}
              </option>
              <option className="brainstorm_subject" value="">
                {" "}
                6
              </option>
              <option className="brainstorm_subject" value="">
                {" "}
                7{" "}
              </option>
              <option className="brainstorm_subject" value="">
                {" "}
                8{" "}
              </option>
              <option className="brainstorm_subject" value="">
                {" "}
                9{" "}
              </option>
              <option className="brainstorm_subject" value="">
                {" "}
                10{" "}
              </option>
              <option className="brainstorm_subject" value="">
                {" "}
                11{" "}
              </option>
              <option className="brainstorm_subject" value="">
                {" "}
                12
              </option>
            </select>

            <p className="instrution">Input each semester's Grade point!</p>

            <input
              className="gp_input"
              placeholder="First year | First semester GP"
            />

            <input
              className="gp_input"
              placeholder="First year | Second semester GP"
            />

            <input
              className="gp_input"
              placeholder="Second year | First semester GP"
            />

            <input
              className="gp_input"
              placeholder="Second year | Second semester GP"
            />

            <input
              className="gp_input"
              placeholder="Third year | First semester GP"
            />

            <input
              className="gp_input"
              placeholder="Third year | second semester GP"
            />

            <input
              className="gp_input"
              placeholder="Fourth year | First semester GP"
            />

            <input
              className="gp_input"
              placeholder="Fourth year | Second semester GP"
            />

            <input
              className="gp_input"
              placeholder="Fifth year | First semester GP"
            />

            <input
              className="gp_input"
              placeholder="Fifth year | Second semester GP"
            />

            <input
              className="gp_input"
              placeholder="Sixth year | First semester GP"
            />

            <input
              className="gp_input"
              placeholder="Sixth year | Second semester GP"
            />

            <button className="move_to_create">Calculate</button>
          </form>
        </div>

        </div>

        }

      </div>
    );

}

