import React, {Component, useState, useEffect, useRef} from 'react';
import HashLoader from 'react-spinners/HashLoader';
import {Link} from 'react-router-dom';

function GenerateInstitution(){
    const  [loading, setLoading] = useState(false);
  useEffect( () => {
      setLoading(true)
      setTimeout(() => {
        setLoading(false)
     }, 5000)
   }, []);
 
    return(
        <div id="full_project_body_container">

            {loading ?

                    <div className="animation_widget">
                        <HashLoader
                        loading={loading} 
                        size={90}
                        marginTop={150}
                        alignSelf={'center'}
                        color={"black"}
                            />
                    </div>

                        :

                        <div>
                            <div className="each_tab_cont">
                            <h2 className="user_suggestion_header">
                            <i className="far fa-chart-bar beside_icon"></i>
                             Institution Generator</h2>

                                            <form className="select_subject_form">

                                            <p className="instrution"> What do you intend to study? <span id="impo"> * </span> </p>
                                                <input className="gp_input" placeholder="Give your answers here." />

                                                <p className="instrution"> What is your preferred insitution of study? <span id="impo"> * </span> </p>
                                                <input className="gp_input" placeholder="Give your answers here" />

                                                <button className="move_to_create">Check for options 
                                                    <i className="fas fa-arrow-right right_icon"></i>
                                                </button>
                                            </form>

                            </div>

                            <div className="each_tab_cont">

                            <p className="user_suggestion_header">
                                            <i className="far fa-chart-bar beside_icon"></i>
                                             Course Generator</p>

                                  

                                            <form className="select_subject_form">
                                                <p className="instrution"> Select Area of Specialization</p>
                                                <Specialization/>


                                                <input class="gp_input" placeholder="Second Selection" />

                                                <button className="move_to_create">
                                                    Check for options <i class="fas fa-arrow-right right_icon"></i>
                                                </button>
                                            </form>
                            </div>

                        </div>
            }

        </div>
    );
}

