import React, { Component } from "react";
import { Link } from "react-router-dom";
import AccountCircleOutlinedIcon from "@material-ui/icons/AccountCircleOutlined";
import HomeOutlinedIcon from "@material-ui/icons/HomeOutlined";
import MailOutlineRoundedIcon from "@material-ui/icons/MailOutlineRounded";
import NotificationsOutlinedIcon from "@material-ui/icons/NotificationsOutlined";
import PeopleOutlineRoundedIcon from "@material-ui/icons/PeopleOutlineRounded";
import SearchRoundedIcon from "@material-ui/icons/SearchRounded";
import LiveTvRoundedIcon from '@material-ui/icons/LiveTvRounded';


class HeaderComp extends Component {
  render() {
    return (
      <div className="top_bar">
        <div className="menu_icon_cont">
            <i className="fas fa-bars" type="header_icons"></i>
        </div>
        <div className="top_header_cont">

          <Link to="/">
          <header className="top_header">
            Edu<span className="header_span">Lens</span>
          </header>
          </Link>

        </div>
        <div className="header_search_box_cont">
          <input
            type="text"
            className="header_search_box"
            placeholder="Hi, Wanna search something?"
          ></input>
          <i className="fi-rr-search search_icon" />
        </div>
        <div className="header_icons_bar">
        <Link to="/" className="header_icons">
            <i className="fas fa-igloo header_icns" />
          </Link>

          <Link to="/chat" className="header_icons">
            <span>9+</span> 
            <i className="fi-rr-envelope header_icns" />
          </Link>

          <Link to="/notification" className="header_icons">
            <span>9+</span> 
            <i className="fi-rr-bell header_icns" />
          </Link>

          <Link to="/" className="header_icons">
            <i className="fi-rr-users header_icns" />
          </Link>

          <Link to="/" className="header_icons">
            <i className="fi-rr-e-learning header_icns" />
          </Link>

          <Link to="/Login" className="header_icons">
            <i className="fi-rr-user header_icons" />
          </Link>
          
        </div>
        <div className="user_icon_cont">
        </div>

        <div className="user_icon_cont">
          <i className="fas fa-search" type="header_icons">
            {" "}
          </i>
      
            <i className="fas fa-user-circle" type="header_icons">
              {" "}
            </i>

        </div>
      </div>
    );
  }
}

export default HeaderComp;
