import React, {Component} from 'react';
import Coder from '../images/coder.jpg';

class MessageMain extends Component{
    render(){
        return(
            <div className="chat_center_widget">
                <div class="app_header_div">
            
                    <div class="app_header_back_navigate_bar">
                        <i className="fi-rr-arrow-small-left"></i>
                    </div>
            
                    <div class="app_header_message_profile_image_bar">
                        <img class="app_header_profile_image" src={Coder} />
                    </div>
            
                    <div class="app_header_title_bar">
                        <p class="app_header_title"> Miracle Umeamotu Saiki </p>
                    </div>
            
                    <div class="app_header_right_navigate_bar">
                        <i className="fas fa-phone"></i>
                        <i className="fas fa-video"></i>
                        <i className="fas fa-info-circle"></i>
                    </div>
    
                </div>
    
    
        <div class="message_largest_div">
            <div>
                <div class="left_side_message_bar" align="left">
                    <img class="left_side-message_profile_pic" src={Coder} /> 
                    <p class="left_side_message">
                        Good Morning Bro! Lorem ipsum dolor sit amet consectetur adipisicing elit. 
                        Quae earum, nulla dicta iusto sequi ullam vel illo enim sunt, impedit fugiat aliquam officia.  
                        Voluptas commodi maiores distinctio atque voluptate quibusdam?
                        </p>
                </div>
    
                <div align="right">
                    <p class="right_side_message" align="right">How are you doing bro?</p>
                </div>
    
                <div class="left_side_message_bar" align="left">
                    <img class="left_side-message_profile_pic" src={Coder} /> 
                    <p class="left_side_message">I'm fine and you?</p>
                </div>
    
                <div align="right">
                    <p class="right_side_message">I'm good too.</p>
                </div>
    
                <div class="left_side_message_bar" align="left">
                    <img class="left_side-message_profile_pic" src={Coder} /> 
                    <p class="left_side_message">Nice! what are you up to?</p>
                </div>
    
                <div align="right">
                    <p class="right_side_message">I'm currently building the Message App.</p>
                </div>
    
                <div class="left_side_message_bar" align="left">
                    <img class="left_side-message_profile_pic" src={Coder} /> 
                    <p class="left_side_message">Alright! That's Awesome.</p>
                </div>
    
                <div class="left_side_message_bar" align="left">
                    <img class="left_side-message_profile_pic" src={Coder} /> 
                    <p class="left_side_message">I'm fine and you?</p>
                </div>
    
                <div align="right">
                    <p class="right_side_message">I'm good too.</p>
                </div>
    
                <div class="left_side_message_bar" align="left">
                    <img class="left_side-message_profile_pic" src={Coder} /> 
                    <p class="left_side_message">Nice! what are you up to?</p>
                </div>
    
                <div align="right">
                    <p class="right_side_message">I'm currently building the Message App.</p>
                </div>
    
                <div class="left_side_message_bar" align="left">
                    <img class="left_side-message_profile_pic" src={Coder} /> 
                    <p class="left_side_message">Alright! That's Awesome.</p>
                </div>
    
            </div>
    
        </div>
    
        <div class="hiding_div_preventive">
    
        </div>
                <div class="message_box_cont">
    
                    <textarea class="message_box_input" placeholder="Write a Message..."></textarea>
    
                    <div class="message_box_icon_bar">
                        <i className="fi-rr-camera"></i>
                        <i className="fi-rr-paper-plane"></i>
                    </div>
                    
                 </div>
    
            </div>
        );
    }
}

export default MessageMain;