import React, { Component, useState, useRef, useEffect } from "react";
import { Link } from 'react-router-dom';
import HashLoader from 'react-spinners/HashLoader';
import lottie from 'lottie-web';
import LottieBrain from '.././LottieAnimation/LottieAnimate';


function BrainstormIndexWidget () {
 
  const  [loading, setLoading] = useState(false);
  useEffect( () => {
      setLoading(true)
      setTimeout(() => {
         setLoading(false)
      }, 5000)
   }, []);
 

   return (

    <div id="full_project_body_container">
                {loading ?
                                            
                                            

                  <div className="animation_widget">
                        <HashLoader
                          loading={loading} 
                          size={90}
                          marginTop={150}
                          alignSelf={'center'}
                          color={"red"}
                            />
                    </div>
              
 
                        : 

   
        <div>
          <div className="each_tab_cont">
                <div className="app_header_div">
                      <div className="app_header_back_navigate_bar">
                        <i className="fas fa-chevron-left" type="header_icons"></i>
                      </div>

                      <div className="app_header_title_bar">
                        <p className="app_header_title"> Brainstorm </p>
                      </div>

                      <div className="app_header_right_navigate_bar2">
                        <i className="fas fa-ellipsis-v" type="header_icons"></i>
                      </div>
                </div>


                <p className="instrution">
                  <i class="fi-rr-user beside_icon"></i> Welcome, 
                  <b> Adewale! </b></p>

                <LottieBrain />

                <p className="instrution">
                  <i className="fi-rr-link beside_icon"></i>
                  Got an invite Link? </p>

                  <form className="select_subject_form">
                    <div>
                      <input type="text" className="gp_input" placeholder="Input Link here" />
                      <i className="fi-rr-search input_right_icon"></i>
                    </div>
                  </form>


                <p className="instrution">
                  <i className="fi-rr-user beside_icon"></i>
                  You can either <b>create</b> or <b>join</b> a brainstorm session.</p>

            <div className="create_join_cont">

                      <Link to="/create-session" className="brainstorm_item_array_cont">
                        <div className="brainstorm_item_array_cont">
                          <i className="fi-rr-plus" type="brainstorm_catalog_icon"
                          ></i>
                        </div>
                      </Link>

                        <div className="brainstorm_item_array_cont">
                          <i className="fi-rr-sign-in" type="brainstorm_catalog_icon"></i>
                        </div>

              </div>


        

            </div>
         </div>

}
        </div>
    );
  };


export default BrainstormIndexWidget;
