import React, {component, useState, useEffect, useRef} from 'react';
import HashLoader from 'react-spinners/HashLoader';
import {Link} from 'react-router-dom';

function WaecYear(){

const  [loading, setLoading] = useState(false);
  useEffect( () => {
      setLoading(true)
      setTimeout(() => {
         setLoading(false)
      }, 5000)
   }, []);
 
    return(
        <div id="full_project_body_container">

            {loading ?

                    <div className="animation_widget">
                        <HashLoader
                        loading={loading} 
                        size={90}
                        marginTop={150}
                        alignSelf={'center'}
                        color={"black"}
                            />
                    </div>

                        :

                        <div>
                            <div className="each_tab_cont">
                            <p className="instrution"> 
                            <i class="fas fa-user-circle beside_icon"></i> Select a Year to start! </p>

                                <div className="year_cont">

                                    <Link to="/waec-questions"> 
                                        <div className="each_year" id="subj_id_catalog_content1"> 
                                            <i className="fas fa-calendar" type="subj_catalog_content_icon"></i>
                                            <p>2010</p>
                                        </div> 
                                        </Link>

                                        <Link to="/waec-questions"> 
                                            <div className="each_year" id="subj_id_catalog_content2"> 
                                                <i className="fas fa-calendar" type="subj_catalog_content_icon"></i>
                                                <p>2011</p> 
                                            </div>
                                        </Link>

                                        <Link to="/waec-questions"> 
                                            <div className="each_year" id="subj_id_catalog_content3"> 
                                                <i className="fas fa-calendar" type="subj_catalog_content_icon"></i>
                                                <p>2012</p>
                                            </div> 
                                        </Link>

                                        <Link to="/waec-questions"> 
                                            <div className="each_year" id="subj_id_catalog_content4"> 
                                                <i className="fas fa-calendar" type="subj_catalog_content_icon"></i>
                                                <p>2013</p>
                                            </div> 
                                        </Link>

                                        <Link to="/waec-questions"> 
                                            <div className="each_year" id="subj_id_catalog_content5"> 
                                                <i className="fas fa-calendar" type="subj_catalog_content_icon"></i>
                                            <p>2014</p>
                                            </div> 
                                        </Link>

                                        <Link to="/waec-questions"> 
                                            <div className="each_year" id="subj_id_catalog_content6"> 
                                                <i className="fas fa-calendar" type="subj_catalog_content_icon"></i>
                                                <p>2015</p>
                                            </div> 
                                        </Link>

                                        <Link to="/waec-questions"> 
                                            <div className="each_year" id="subj_id_catalog_content7"> 
                                                <i className="fas fa-calendar" type="subj_catalog_content_icon"></i>
                                                <p>2016</p> 
                                            </div> 
                                        </Link>

                                        <Link to="/waec-questions"> 
                                            <div className="each_year" id="subj_id_catalog_content8"> 
                                                <i className="fas fa-calendar" type="subj_catalog_content_icon"></i> 
                                                <p>2017</p>
                                            </div> 
                                        </Link>

                                        <Link to="/waec-questions"> 
                                            <div className="each_year" id="subj_id_catalog_content9"> 
                                                <i className="fas fa-calendar" type="subj_catalog_content_icon"></i>
                                                <p>2018</p>
                                            </div> 
                                        </Link>

                                        <Link to="/waec-questions"> 
                                            <div className="each_year" id="subj_id_catalog_content10"> 
                                                <i className="fas fa-calendar" type="subj_catalog_content_icon"></i>
                                                <p>2019</p>
                                            </div> 
                                        </Link>

                                        <Link to="/waec-questions"> 
                                            <div className="each_year" id="subj_id_catalog_content10"> 
                                                <i className="fas fa-calendar" type="subj_catalog_content_icon"></i>
                                                <p>2020</p>
                                            </div> 
                                        </Link>

                                </div>

                            </div>
                        </div>
        }

        </div>
    );
}

export default WaecYear;