import React, {component} from 'react';

function CreateQuestion (){
    return(
        <div id="full_project_body_container">
            <div className="each_tab_cont">
                <p className="instrution"><i className="fi-rr-plus beside_icon"></i> <b>Adewale, </b>  Create your own questions! </p>

                <form className="select_subject_form" action="brainstorm-players-interface.php">

                    <p className="input_desc">* Select number of Questions</p>

                    <select className="select_subject_bar" name="select_number_question">
                        <option className="brainstorm_subject" value=""> Select number of Questions... </option>
                        <option className="brainstorm_subject" value=""> 1 </option> 
                        <option className="brainstorm_subject" value=""> 2 </option> 
                        <option className="brainstorm_subject" value=""> 3 </option> 
                        <option className="brainstorm_subject" value=""> 4 </option> 
                        <option className="brainstorm_subject" value=""> 5 </option>           
                    </select>


                    <p className="input_desc">Question <span>(1)</span> </p>
                        <textarea className="question_input" type="text" name="user_question"> </textarea>


                        <input className="option_input" type="text" name="user_question" placeholder="Option A" />
                        <input className="option_input" type="text" name="user_question" placeholder="Option B" />
                        <input className="option_input" type="text" name="user_question" placeholder="Option C" />
                        <input className="option_input" type="text" name="user_question" placeholder="Option D" />

                        <input className="option_correct_input" type="text" name="user_question" placeholder="Correct Answer" />
                        <i className="fi-rr-check input_right_icon"></i>
                    <p className="input_desc"></p>

                    <button className="move_to_create"> Next Question </button>
                </form>
            </div>
        </div>
    );
}

export default CreateQuestion;