import React, {Component} from 'react';
import Coder from '../images/coder.jpg';

class ChatMessage extends Component{
    render(){
        return(
                <div className="chat_left_widget">
                     <div class="app_header_div">
                
                        <div class="app_header_title_bar">
                            <p class="app_header_title"> 
                            <i className="fi-rr-envelope beside_icon"></i>Chats </p>
                        </div>
                
                        <div class="app_header_right_navigate_bar">
                            <i className="fi-rr-users"></i>
                            <i className="fi-rr-edit"></i>
                            <i className="fi-rr-menu-dots"></i>
                        </div>

                    </div>

                    <form className="search_chat_cont">
                        <input placeholder="Search Chat" />
                        <i className="fi-rr-search"></i>
                    </form>

                    <div className="parent_div">

                        <div className="chat_each_user_cont">
                            <img className="chat_user_image" src={Coder} />

                            <div className="chat_text_div">
                                <p className="chat_username"> Leinad Priestley</p>
                                <p className="chat_message"> Good Morning!</p>
                            </div>

                            <div className="chat_icon_div">
                                <p className="chat_last_message_time">12:18 PM</p>
                            </div>
                        </div>

                        <div className="chat_each_user_cont">
                            <img className="chat_user_image" src={Coder} />

                            <div className="chat_text_div">
                                <p className="chat_username">Miracle Sykee</p>
                                <p className="chat_message">Your head!</p>
                            </div>

                            <div className="chat_icon_div">
                                <p className="chat_last_message_time">2:30 PM</p>
                            </div>
                        </div>

                        <div className="chat_each_user_cont">
                            <img className="chat_user_image" src={Coder} />

                            <div className="chat_text_div">
                                <p className="chat_username">Femi Fatokun</p>
                                <p className="chat_message">Awesome Bro!</p>
                            </div>

                            <div className="chat_icon_div">
                                <p className="chat_last_message_time">2:32 PM</p>
                            </div>
                        </div>

                        <div className="chat_each_user_cont">
                            <img className="chat_user_image" src={Coder} />

                            <div className="chat_text_div">
                                <p className="chat_username">Idris Abdulazeez</p>
                                <p className="chat_message">Bro!</p>
                            </div>

                            <div className="chat_icon_div">
                                <p className="chat_last_message_time">3:00 PM</p>
                            </div>
                        </div>

                        <div className="chat_each_user_cont">
                            <img className="chat_user_image" src={Coder} />

                            <div className="chat_text_div">
                                <p className="chat_username">Samson Onifade</p>
                                <p className="chat_message">Are you at home?</p>
                            </div>

                            <div className="chat_icon_div">
                                <p className="chat_last_message_time">4:12 PM</p>
                            </div>
                        </div>

                    </div>

                </div>
        );
    }
}

export default ChatMessage;