import React, {Compoent, useEffect, useRef} from 'react';
import lottie from "lottie-web";

function CbtAnimate (){

    const container = useRef(null);

    useEffect(() =>{
        lottie.loadAnimation({
            container:container.current,
            renderer:"svg",
            loop:true,
            autoplay:true,
            animationData: require("../.././computerEth.json"),
        });
    }, []);

    return (
        <div className="Lottie_display" ref={container}></div>
    );

};

export default CbtAnimate;