import React, {Component} from 'react';
import ChatMessage from './chatMessages';
import MessageMain from './MessageMain';
import ChatInfo from './chatInfo';

class Chat extends Component{
    render(){
        return(
            
            <div className="index_biggest_cont">
                    <ChatMessage />
                    <MessageMain />
                    <ChatInfo />
            </div>
        );
    }
}

export default Chat;